<template>
  <div
    class="min-h-screen flex items-center justify-center bg-gray-50 py-12 px-4 sm:px-6 lg:px-8"
  >
    <div class="max-w-md w-full space-y-8">
      <LoginLogo />

      <form class="mt-8 space-y-6" @submit.prevent="handleSubmit">
        <div class="rounded-md shadow-sm -space-y-px">
          <BaseInput
            type="text"
            v-model="state.username"
            placeholder="ชื่อผู้ใช้"
            @blurInput="v$.username.$touch()"
            :inputValid="!v$.username.$error"
            :errorMessages="v$.username"
          />
          <div class="pt-4"></div>
          <BaseInput
            type="password"
            v-model="state.password"
            placeholder="รหัสผ่าน"
            @blurInput="v$.password.$touch()"
            :inputValid="!v$.password.$error"
            :errorMessages="v$.password"
          />
        </div>
        <div>
          <AppButton
            color="bg-red-400"
            text="เข้าสู่ระบบ"
            :isDisabled="this.v$.$invalid"
          />
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import useValidate from "@vuelidate/core";
import { required, minLength, helpers } from "@vuelidate/validators";
import { reactive, computed } from "@vue/reactivity";
import AppButton from "@/components/Button";
import BaseInput from "@/components/BaseInput.vue";
import LoginLogo from "@/components/LoginLogo";
import router from "@/router";
import store from "@/store";

export default {
  components: { AppButton, BaseInput, LoginLogo },
  setup() {
    const initData = {
      username: "",
      password: "",
    };

    const state = reactive({
      username: "",
      password: "",
    });

    const showAlert = reactive({ status: false });

    const rules = computed(() => {
      return {
        username: {
          required: helpers.withMessage("กรุณากรอกชื่อผู้ใช้งาน", required),
        },
        password: {
          required: helpers.withMessage("กรุณากรอกรหัสผ่าน", required),
          minLength: helpers.withMessage(
            () => "กรุณากรอกรหัสผ่าน 6 ตัวอักษร",
            minLength(6)
          ),
        },
      };
    });

    const v$ = useValidate(rules, state);

    return { initData, state, v$, showAlert };
  },
  methods: {
    handleSubmit() {
      console.log(this.state.username);
      console.log(this.state.password);
      store.dispatch("setAuthentication", true);
      router.push({ name: "Home" });

      // this.v$.$validate();
      // if (!this.v$.$error) {
      //   console.log(this.state.username);
      //   console.log(this.state.password);
      //   store.dispatch("setAuthentication", true);
      //   router.push({ name: "Home" });
      // } else {
      //   alert("Fail");
      //   // this.resetForm();
      // }
    },
    resetForm() {
      this.v$.$reset();
      this.state = { ...this.initData };
    },
  },
};
</script>

