import { createStore } from "vuex";
import createPersistedState from "vuex-persistedstate";
import SecureLS from "secure-ls";
var ls = new SecureLS({ isCompression: false });

const initState = () => {
  return {
    count: 0,
    activeMenu: "/home",
    authenticated: false,
  }
}

export default createStore({
  
  state: initState(),
  mutations: {
    devChangeData(state, value) {
      state.count = value;
    },
    changeActiveMenu(state, value) {
      state.activeMenu = value;
    },
    changeAuthentication(state, status) {
      state.authenticated = status;
    },
    setClearAuthentication(state) {
      Object.assign(state, initState());
    },
  },
  getters: {
    getCount(state) {
      return state.count;
    },
    getActiveMenu(state) {
      return state.activeMenu;
    },
    getAuthenticated(state) {
      return state.authenticated;
    },
  },
  actions: {
    increaseCount(context) {
      context.commit("devChangeData", this.getters.getCount + 1);
    },
    decreaseCount(context) {
      context.commit("devChangeData", this.getters.getCount - 1);
    },
    changeMenu(context, menu) {
      context.commit("changeActiveMenu", menu);
    },
    setAuthentication(context, status) {
      context.commit("changeAuthentication", status);
    },
    clearAuthentication(context) {
      context.commit("setClearAuthentication");
    },
  },
  modules: {},
  plugins: [
    createPersistedState({
      storage: {
        getItem: (key) => ls.get(key),
        setItem: (key, value) => ls.set(key, value),
        removeItem: (key) => ls.remove(key),
      },
    }),
  ],
});
