<template>
  <div>
    <textarea
      :value="modelValue"
      @input="updateInput"
      :rows="rows"
      :class="InputClass"
      :placeholder="placeholder"
    ></textarea>

  </div>
</template>

<script>
import { ref } from "vue";
export default {
  name: "TextareaInput",
  props: {
    modelValue: {
      type: [String, Number],
      default: "",
    },
    placeholder: {
      type: String,
      default: "text",
    },
     rows: {
      type: Number,
      default: 3,
    },
  },
  emits: ["update:modelValue"],
  setup(props, { emit }) {
    const InputClass = ref(
      "w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 focus:z-10 sm:text-sm rounded-md"
    );

    const updateInput = (event) => {
      emit("update:modelValue", event.target.value);
    };

    return {
      InputClass,
      updateInput,
    };
  },
};
</script>
