  </div>
</template>

<script>
export default {
  props: {
    placeHolder: {
      type: String,
      default: "ค้นหา",
    },
    modelValue: {
      type: [String, Number],
      default: "",
    },
  },
  methods: {
    updateInput(event) {
      this.$emit("update:modelValue", event.target.value);
    },
    clearKeyword() {
      this.$emit("update:modelValue", "");
      this.$emit("doClear");
    },
    searchByKeyword() {
      this.$emit("doSearch");
    },
  },
};
</script>

