import { createRouter, createWebHistory } from "vue-router";
import Login from "@/views/Login.vue";
import Home from "@/views/Home.vue";
import Order from "@/views/Order.vue";
import Stock from "@/views/Stock.vue";
import Search from "@/views/Search.vue";
import Report from "@/views/Report.vue";
import Member from "@/views/members/Member.vue";
import Company from "@/views/Company.vue";
import NotFound from "@/views/NotFound.vue";
import AddMember from "@/views/members/AddMember.vue";
import EditMember from "@/views/members/EditMember.vue";
import ValidateDemo from "@/views/ValidateDemo.vue";
import store from "@/store";

const checkAuthenticated = (to, from, next) => {
  if (store.getters.getAuthenticated == false) {
    next("/");
  } else {
    next();
  }
};

const clearAllStateAuthenticated = () => {
  store.dispatch("clearAuthentication");
};

const routes = [
  {
    path: "/",
    name: "Login",
    component: Login,
    beforeEnter: clearAllStateAuthenticated,
  },
  {
    path: "/home",
    name: "Home",
    component: Home,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/order",
    name: "Order",
    component: Order,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/stock",
    name: "Stock",
    component: Stock,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/search",
    name: "Search",
    component: Search,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/report",
    name: "Report",
    component: Report,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/member",
    name: "Member",
    component: Member,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/company",
    name: "Company",
    component: Company,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/add-member",
    name: "AddMember",
    component: AddMember,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/edit-member",
    name: "EditMember",
    component: EditMember,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/validate-demo",
    name: "ValidateDemo",
    component: ValidateDemo,
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/all-order",
    redirect: "/order",
    beforeEnter: checkAuthenticated,
  },
  {
    path: "/:catchAll(.*)",
    name: "NotFound",
    component: NotFound,
    beforeEnter: checkAuthenticated,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
