<template>
  <nav class="bg-green-800">
    <div class="max-w-7xl mx-auto px-2 sm:px-6 lg:px-8">
      <div class="relative flex items-center justify-between h-16">
        <div
          class="flex-1 flex items-center justify-center sm:items-stretch sm:justify-start"
        >
          <div class="flex-shrink-0 flex items-center">
            <img
              class="mx-auto h-10 w-auto"
              src="../assets/images/pharmacy.png"
            />
          </div>
          <div class="hidden sm:block sm:ml-6">
            <div class="flex space-x-4">
              <div
                :class="currentRout == '/home' ? activeClass : btnClass"
                @click="menuClick('/home')"
              >
                หน้าหลัก
              </div>
              <div
                :class="currentRout == '/order' ? activeClass : btnClass"
                @click="menuClick('/order')"
              >
                ระบบขาย
              </div>
              <div
                :class="currentRout == '/stock' ? activeClass : btnClass"
                @click="menuClick('/stock')"
              >
                การการสินค้า
              </div>
              <div
                :class="currentRout == '/search' ? activeClass : btnClass"
                @click="menuClick('/search')"
              >
                ค้นหาสินค้า
              </div>
              <div
                :class="currentRout == '/report' ? activeClass : btnClass"
                @click="menuClick('/report')"
              >
                ออกรายงาน
              </div>
              <div
                :class="currentRout == '/member' ? activeClass : btnClass"
                @click="menuClick('/member')"
              >
                สมาชิก
              </div>
              <div
                :class="currentRout == '/company' ? activeClass : btnClass"
                @click="menuClick('/company')"
              >
                บริษัทคู๋ค้า
              </div>
            </div>
          </div>
        </div>

        <div
          class="absolute inset-y-0 right-0 flex items-center pr-2 sm:static sm:inset-auto sm:ml-6 sm:pr-0"
        >
          <div :class="btnLogoutClass" @click="menuClick('/')">
            ออกจากระบบ
          </div>
        </div>
      </div>
    </div>
  </nav>
  <Confirm
    v-if="showConfirm"
    icon="warning"
    title="ออกจากระบบ"
    message="กรุณายืนยันก่อนออกจากระบบ"
    btnCancel="ยกเลิก"
    btnOkay="ตกลง"
    @accepted="confirmDismiss"
  />
</template>

<script>
import router from "@/router";
import store from "@/store";
import { ref } from "@vue/reactivity";
import Confirm from "@/components/Confirm.vue";

export default {
  components: { Confirm },
  mounted() {
    console.log("mounted!");
    console.log(navigator.onLine);
  },
  setup() {
    const currentRout = ref(router.currentRoute.value.path);
    const showConfirm = ref(false);

    const btnClass = ref(
      "text-white px-3 py-2 rounded-md text-sm font-medium hover:bg-green-700 hover:text-white"
    );
    const activeClass = ref(
      "bg-gray-900 text-white px-3 py-2 rounded-md text-sm font-medium hover:bg-gray-900 hover:text-white"
    );
    const btnLogoutClass = ref(
      "bg-red-900 text-white px-3 py-2 rounded-md text-sm font-medium hover:bg-red-700 hover:text-white"
    );

    const menuClick = (path) => {
      if (path == "/") {
        store.dispatch("setAuthentication", false);
        showConfirm.value = true;
      } else {
        store.dispatch("changeMenu", path);
        router.push(path);
      }
    };

    const confirmDismiss = (res) => {
      showConfirm.value = false;
      console.log(res);
      if (res) {
        router.push("/");
      }
    };

    return {
      menuClick,
      activeClass,
      btnClass,
      btnLogoutClass,
      currentRout,
      showConfirm,
      confirmDismiss,
    };
  },
};
</script>

