<template>
  <NavBar />
  <vue-highcharts
    type="chart"
    :options="chartOptions"
    :redrawOnUpdate="true"
    :oneToOneUpdate="false"
    :animateOnUpdate="true"
    @rendered="onRender"
  />
</template>

<script>
import NavBar from "@/components/NavBar";
import VueHighcharts from "vue3-highcharts";
import { ref, computed } from "vue";
export default {
  name: "Report",
  components: { NavBar, VueHighcharts },
  setup() {
    const seriesData1 = ref([
      49.9,
      71.5,
      106.4,
      129.2,
      144.0,
      176.0,
      135.6,
      148.5,
      216.4,
      194.1,
      95.6,
      54.4,
    ]);

    const seriesData2 = ref([
      83.6,
      78.8,
      98.5,
      93.4,
      106.0,
      84.5,
      105.0,
      104.3,
      91.2,
      83.5,
      106.6,
      92.3,
    ]);

    const categories = ref([
      "Jan",
      "Feb",
      "Mar",
      "Apr",
      "May",
      "Jun",
      "Jul",
      "Aug",
      "Sep",
      "Oct",
      "Nov",
      "Dec",
    ]);

    const chartOptions = computed(() => ({
      chart: {
        type: "column",
      },
      title: {
        text: "Monthly Average Rainfall",
      },
      subtitle: {
        text: "Source: WorldClimate.com",
      },
      xAxis: {
        categories: categories.value,
        crosshair: true
      },
      yAxis: {
        min: 0,
        title: {
            text: 'Rainfall (mm)'
        },
      },
      tooltip: {
        headerFormat: '<span style="font-size:10px">{point.key}</span><table>',
        pointFormat:
          '<tr><td style="color:{series.color};padding:0">{series.name}: </td>' +
          '<td style="padding:0"><b>{point.y:.1f} mm</b></td></tr>',
        footerFormat: "</table>",
        shared: true,
        useHTML: true,
      },
      series: [
        {
          name: "Salaya",
          data: seriesData1.value,
        },
        {
          name: "Classday",
          data: seriesData2.value,
        },
      ],
      plotOptions: {
        column: {
          pointPadding: 0.1,
          borderWidth: 0,
        },
      },
    }));

    const onRender = () => {
      console.log("Chart rendered");
    };

    return {
      chartOptions,
      onRender,
    };
  },
};
</script>

<style>
.vue-highcharts {
  width: 100%;
}
</style>
