import HttpRequest from "./http_request";

class UserProvider extends HttpRequest {
  constructor() {
    super("http://localhost:3000");
  }

  async getUser() {
    const { data } = await this.get("/jobs");
    return data;
  }

  async getMembers() {
    const { data } = await this.get("/members");
    return data;
  }

  async addMember(params) {
    const { status } = await this.create("/members", params);
    return status;
  }

  async deleteMember(id) {
    const { status } = await this.delete("/members/" + id);
    return status;
  }

  async getMemberById(id) {
    const { data } = await this.get("/members/" + id);
    return data;
  }

  async editMember(id, params) {
    const { status } = await this.update("/members/" + id, params);
    return status;
  }
}

export default UserProvider;
