<template>
  <div>
    <input
      :type="type"
      :value="modelValue"
      @input="updateInput"
      :class="InputClass"
      :placeholder="placeholder"
      @blur="blurInput"
    />
  </div>
  <div v-if="!inputValid">{{ errorMessages.$errors[0].$message }}</div>
</template>

<script>
import { ref } from "vue";
export default {
  name: "BaseInput",
  props: {
    modelValue: {
      type: [String, Number],
      default: "",
    },
    type: {
      type: String,
      default: "text",
    },
    placeholder: {
      type: String,
      default: "Text here",
    },
    inputValid: {
      type: Boolean,
      default: true,
    },
    errorMessages: {
      type: Object,
      default: null,
    },
  },
  emits: ["update:modelValue", "blurInput"],
  setup(props, { emit }) {
    const InputClass = ref(
      "appearance-none rounded-md relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 focus:z-10 sm:text-sm"
    );

    const updateInput = (event) => {
      emit("update:modelValue", event.target.value);
    };

    const blurInput = () => {
      emit("blurInput");
    };

    return {
      InputClass,
      updateInput,
      blurInput,
    };
  },
};
</script>
