<template>
  <div class="home">
    <h2>Create a account</h2>
    <p>
      <input
        type="text"
        placeholder="email"
        v-model="state.email"
        @blur="v$.email.$touch()"
      />
      <span v-if="v$.email.$error">
        {{ v$.email.$errors[0].$message }}
      </span>
    </p>
    <p>
      <input
        type="password"
        placeholder="password"
        v-model="state.password.password"
        @blur="v$.password.password.$touch()"
      />
      <span v-if="v$.password.password.$error">
        {{ v$.password.password.$errors[0].$message }}
      </span>
    </p>
    <p>
      <input
        type="password"
        placeholder="Confirm password"
        v-model="state.password.confirm"
        @blur="v$.password.confirm.$touch()"
      />
      <span v-if="v$.password.confirm.$error">
        {{ v$.password.confirm.$errors[0].$message }}
      </span>
    </p>
    <button @click="submitForm">Submit</button>
  </div>
</template>

<script>
import useValidate from "@vuelidate/core";
import {
  required,
  email,
  minLength,
  sameAs,
  helpers,
} from "@vuelidate/validators";
import { reactive, computed } from "vue";

export default {
  name: "ValidateDemo",
  setup() {
    const initData = {
      email: "",
      password: {
        password: "",
        confirm: "",
      },
    };

    const state = reactive({
      email: "",
      password: {
        password: "",
        confirm: "",
      },
    });

    const mustBeLearnVue = (value) => value.includes("gmail");
    const rules = computed(() => {
      return {
        email: {
          required,
          email,
          mustBeLearnVue: helpers.withMessage(
            "Must be Gmail.com",
            mustBeLearnVue
          ),
        },
        password: {
          password: { required, minLength: minLength(6) },
          confirm: { required, sameAs: sameAs(state.password.password) },
        },
      };
    });

    const v$ = useValidate(rules, state);

    return {
      state,
      v$,
      initData,
    };
  },
  methods: {
    submitForm() {
      // console.log(this.v$);
      this.v$.$validate();
      if (!this.v$.$error) {
        console.log(this.state.email);
        console.log(this.state.password.password);
        console.log(this.state.password.confirm);
        this.reset();
      } else {
        alert("Fail");
      }
    },
    reset() {
        this.v$.$reset();
        this.state = { ...this.initData };
    },
  },
};
</script>
