<template>
  <NavBar />
  <div class="py-8 px-10">
    <div class="md:grid md:grid-cols-5 md:gap-6">
      <div class="md:col-span-1">
        <div class="px-4 sm:px-0">
          <h3 class="text-lg font-medium leading-6 text-gray-900">ระสมาชิก</h3>
          <p class="mt-1 text-sm text-gray-600">
            แก้ไขสมาชิกในระบบ
          </p>
        </div>
      </div>
      <div class="md:col-span-4">
        <form class="px-4 sm:px-0" @submit.prevent="handleSubmit">
          <div class="pt-4">
            <BaseInput type="text" v-model="customerName" placeholder="ชื่อ" />
          </div>
          <div class="pt-4">
            <BaseInput
              type="text"
              v-model="customerLastName"
              placeholder="นามสกุล"
            />
          </div>
          <div class="pt-4">
            <TextareaInput v-model="customerAddress" placeholder="ที่อยู่" />
          </div>
          <div class="pt-4">
            <BaseInput
              type="text"
              v-model="customerMobile"
              placeholder="เบอร์ติดต่อ"
            />
          </div>
          <div class="pt-4">
            <TextareaInput
              v-model="customerDrugAllergy"
              placeholder="ข้อมูลการแพ้ยา"
              :rows="5"
            />
          </div>
          <div class="pt-4">
            <AppButton color="bg-red-600" text="แก้ไขข้อมูลสมาชิก" />
          </div>
        </form>
        <div class="pt-6">
          <AlertDialog
            v-if="showAlert"
            @closePressed="close"
            erroMessage="กรุณาตรวจสอบข้อมูลให้ครบถ้วน"
            color="bg-red-400"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from "@/components/NavBar";
import { ref, onMounted } from "vue";
import AppButton from "@/components/Button";
import BaseInput from "@/components/BaseInput.vue";
import TextareaInput from "@/components/TextareaInput.vue";
import AlertDialog from "@/components/AlertDialog";
import router from "@/router";
import UserProvider from "@/resources/user_provider";

export default {
  name: "EditMember",
  components: { NavBar, AppButton, BaseInput, AlertDialog, TextareaInput },

  setup() {
    const customerName = ref("");
    const customerLastName = ref("");
    const customerAddress = ref("");
    const customerDrugAllergy = ref("");
    const customerMobile = ref("");
    const showAlert = ref(false);

    onMounted(() => {
      console.log("mounted");
      getMembers();
    });

    const getMembers = async () => {
      const userService = new UserProvider();
      const data = await userService.getMemberById(1);

      customerName.value = data.name;
      customerLastName.value = data.lastName;
      customerAddress.value = data.address;
      customerDrugAllergy.value = data.drugAllergy;
      customerMobile.value = data.mobile;
    };

    const handleSubmit = async () => {
      const userService = new UserProvider();

      if (customerName.value !== "" && customerLastName.value !== "") {
        let objMember = {
          name: customerName.value,
          lastName: customerLastName.value,
          address: customerAddress.value,
          drugAllergy: customerDrugAllergy.value,
          mobile: customerMobile.value,
        };

        const response = await userService.editMember(1, objMember);
        console.log(response);
        if (response == "200 ") {
          router.push({ name: "Member" });
        } else {
          console.log(response);
        }
      } else {
        showAlert.value = true;
      }
    };

    const close = () => {
      showAlert.value = false;
    };

    return {
      customerName,
      customerLastName,
      customerAddress,
      customerDrugAllergy,
      customerMobile,
      handleSubmit,
      showAlert,
      close,
    };
  },
};
</script>
